import { Component, OnInit, ViewChild } from '@angular/core';
import { MainService } from 'src/app/services/main.service';

@Component({
  selector: 'app-playlists',
  templateUrl: './playlists.component.html',
  styleUrls: ['./playlists.component.css']
})
export class PlaylistsComponent implements OnInit {

  myString = 'Hello World!';
  title = 'Here goes the subtitles!';
  showTrans = false;
  paginationCurrent = 1;
  paginationLimit = 10;
  paginationMax: number = 10;
  paginationShowItems: any;
  indexArr: any[] = [];

  constructor(private mainService: MainService) { }

  ngOnInit() {
    this.mainService.getVideoCount().subscribe(
      res => {
        this.paginationMax = Math.floor(parseInt(res['count'])/this.paginationLimit);
        console.log(res,this.paginationMax);
        if(this.paginationMax == 0) {
          this.indexArr = this.getArrayOfIndex(this.paginationMax + 1);
        } else {
          this.indexArr = this.getArrayOfIndex(this.paginationMax);
        }
        // let index = this.paginationMax + 1;
        // console.log(index);
        
        console.log(this.indexArr);
      }
      
    );

    this.mainService.getPlaylists(this.paginationCurrent, this.paginationLimit).subscribe(res => {
      this.paginationShowItems = res;
      console.log('recorded check : ', res);
    });
  }

  getArrayOfIndex(i: number) {
    return new Array(i);
  }

  paginationNext() {
    if ( this.paginationCurrent !== this.paginationMax) {
      this.paginationCurrent++;
      this.mainService.getPlaylists(this.paginationCurrent, this.paginationLimit).subscribe(result =>{
        console.log('Result Check : ', result);
      });
    }
  }

  paginationNumber(event: any) {
    console.log('Current Button pressed : ', event.target.value);
    this.paginationCurrent = event.target.value;
    this.mainService.getPlaylists(this.paginationCurrent, this.paginationLimit).subscribe(result =>{
      console.log('Result Check : ', result);
    });
  }

  paginationPrevious() {
    if ( this.paginationCurrent !== 0) {
      this.paginationCurrent--;
      this.mainService.getPlaylists(this.paginationCurrent, this.paginationLimit).subscribe(result =>{
        console.log('Result Check : ', result);
      });
    }
  }
  showTranscript() {
    this.showTrans = !this.showTrans;
  }
}