import {
  Component,
  OnInit,
  ViewChild,
  AfterViewInit,
  ElementRef
} from '@angular/core';

@Component({
  selector: 'app-live',
  templateUrl: './live.component.html',
  styleUrls: ['./live.component.css']
})
export class LiveComponent implements OnInit, AfterViewInit {
  myString = 'Hello World!';
  title = 'Here goes the subtitles!';
  showTrans: boolean = false;

  

  constructor(private elementRef: ElementRef) {}

  ngOnInit() {
    console.log('Full Screen Element : ', document.fullscreenElement);
  }
  ngAfterViewInit() {

    let cue = new VTTCue(0, 30, 'Cool text to be displayed');

    const video = this.elementRef.nativeElement.querySelector('video');

  //   if(Hls.isSupported()) {
  //     var hls = new Hls();
  //     hls.loadSource('https://video-dev.github.io/streams/x36xhzz/x36xhzz.m3u8');
  //     hls.attachMedia(video);
  //     hls.on(Hls.Events.MANIFEST_PARSED,function() {
  //       video.play();
  //   });
  //  }

    video.addEventListener('loadedmetadata', function() {
      let track = document.createElement('track');
      track.kind = 'captions';
      track.label = 'English';
      track.srclang = 'en';
      track.src = 'assets/scripts/test_transcript.vtt';
      track.setAttribute('default', 'default');
      track.addEventListener('load', () =>  {
        video.textTracks[0].mode = 'showing';
      });
      this.appendChild(track);
    });

    video.textTracks.addEventListener('change', () => {
        video.textTracks[0].addCue(cue);
      },
      false
    );
  }
}
