import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './components/main/main.component';
import { LiveComponent } from './components/live/live.component';
import { PlaylistsComponent } from './components/playlists/playlists.component';
import { PlayerComponent } from './components/player/player.component';
import { TestPlayerComponent } from './components/test-player/test-player.component';

const routes: Routes = [
  {path: 'main', component: MainComponent},
  {path: 'live', component: LiveComponent},
  {path: '', redirectTo: '/live', pathMatch: 'full'},
  {path: 'playlist', component: PlaylistsComponent},
  {path: 'playlist/player', component: PlayerComponent},
  {path: 'testplayer', component: TestPlayerComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
